/* Base styles for all devices */
.video-player-container {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  width: 100%;
  background-color: #000000;
  padding: 20px;
  box-sizing: border-box;
}

.video-wrapper {
  position: relative;
  width: 100%;
  max-width: 900px;
  padding-top: 56.25%; /* 16:9 Aspect Ratio */
}

.video-iframe {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  border: none;
}

.placeholder-image {
  width: 100%;
  height: auto;
}

.no-video-message {
  text-align: center;
  font-size: 24px;
  color: #333;
}

/* Styles for devices with width >= 1024px (Desktops) */
@media (min-width: 1024px) {
  .video-wrapper {
    width: 80%;
    max-width: 900px;
  }
}

/* Styles for devices with width between 768px and 1024px (Tablets) */
@media (min-width: 768px) and (max-width: 1024px) {
  .video-wrapper {
    width: 90%;
    max-width: 768px;
    padding-top: 56.25%; /* Maintain 16:9 Aspect Ratio */
  }
}

/* Styles for devices with width < 768px (Mobile) */
@media (max-width: 768px) {
  .video-wrapper {
    width: 100%;
    max-width: none;
    padding-top: 56.25%; /* Maintain 16:9 Aspect Ratio */
  }

  .no-video-message {
    font-size: 18px;
  }
}

/* Landscape and Portrait orientations */
@media (orientation: landscape) {
  .video-wrapper {
    padding-top: 56.25%; /* Maintain 16:9 Aspect Ratio */
  }
}

@media (orientation: portrait) {
  .video-wrapper {
    padding-top: 56.25%; /* Maintain 16:9 Aspect Ratio */
  }
}
